<template>
  <div class="pa-6">
    <v-card color="basil">
      <v-card-title class="text-center py-6 primary--text headline">
        <v-btn
          @click="getTableOfExit()"
          x-small fab color="primary" class="mr-3 elevation-0"
        >
          <v-icon class="black--text" small>fas fa-redo-alt</v-icon>
        </v-btn>
        Table of Exit
        <v-spacer />
        <span class="white--text">
          <span class="primary--text pr-2">₱</span>
          {{ money(selectedToe.value) }}
        </span>
      </v-card-title>
      <v-tabs
        v-model="tab"
        background-color="primary"
        grow center-active
        slider-color="black"
      >
        <v-tab
          v-for="(item, index) in tableOfExits" :key="index"
          class="black--text"
          :disabled="index > (tableOfExit.latest_table_no - 1)"
          :class="tabColors[index]"
        >
          {{ item.name }}
        </v-tab>
      </v-tabs>
      <div>
        <v-card v-if="!loading" flat class="pa-5">
          <v-card-text>
            <v-row>
              <v-col cols="12" class="pa-1">
                <table-of-exit-seat :seat="tableOfExit.position1" />
              </v-col>
              <v-col cols="12" md="6" sm="12" class="pa-1">
                <table-of-exit-seat :seat="tableOfExit.position2" />
              </v-col>
              <v-col cols="12" md="6" sm="12" class="pa-1">
                <table-of-exit-seat :seat="tableOfExit.position3" />
              </v-col>
              <v-col cols="12" md="3" sm="12" class="pa-1">
                <table-of-exit-seat :seat="tableOfExit.position4" />
              </v-col>
              <v-col cols="12" md="3" sm="12" class="pa-1">
                <table-of-exit-seat :seat="tableOfExit.position5" />
              </v-col>
              <v-col cols="12" md="3" sm="12" class="pa-1">
                <table-of-exit-seat :seat="tableOfExit.position6" />
              </v-col>
              <v-col cols="12" md="3" sm="12" class="pa-1">
                <table-of-exit-seat :seat="tableOfExit.position7" />
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
        <div v-else style="min-height: 580px;">
          <page-loading style="margin-top: -100px;" />
        </div>
      </div>
    </v-card>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import PageLoading from '@/components/PageLoading'
import TableOfExitSeat from '@/components/TableOfExitSeat'

export default {
  components: {
    PageLoading,
    TableOfExitSeat
  },
  data: () => ({
    tab: null,
    loading: true,
    selectedToe: {},
    tableOfExit: {},
    text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.',
    tabColors: ['blue', 'primary', 'teal lighten-1', 'light-blue darken-4', 'red darken-3']
  }),
  computed: {
    ...mapGetters({
      selectedAccount: 'selectedAccount',
      tableOfExits: 'tableOfExits'
    })
  },
  watch: {
    tab (newV) {
      this.selectedToe = this.tableOfExits[newV]
      this.getTableOfExit()
    },
    selectedAccount () {
      this.tab = 0
      this.getTableOfExit()
    }
  },
  methods: {
    async getTableOfExit () {
      try {
        this.loading = true
        const tableOfExit = await this.$axios.get('/user/table-of-exits', {
          params: { account_id: this.selectedAccount.id, table_level: this.selectedToe.level }
        })
        this.tableOfExit = tableOfExit.data.data
      } catch (e) {
        this.tableOfExit = {}
      } finally {
        this.loading = false
      }
    }
  }
}
</script>
